import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
//import { organismos } from '../../json/organismos';
import Modal from 'react-bootstrap/Modal';
import './organismos.css';
import Error404 from '../Error404/Error404';

const Informacion = () => {
    const { nombre } = useParams();
    const [modal, setModal] = useState({
        ver: false,
        tipo: "",
        titulo: "",
    })

    const [arreglo, setArreglo] = useState([])
    useEffect(() => {
        window.scrollTo(0, 0);
        const arreglin = window._my_informacion.filter(p => p.enlace===nombre)
        if(arreglin.length>0){
            setArreglo(arreglin)
        }
    }, [nombre]);


    let filtrado = []
    let filtradoTemas = []
    if(arreglo.length>0){
        filtrado = arreglo[0].sub.filter(p => p.titulo===modal.tipo)
        if(filtrado.length>0){
            filtradoTemas = filtrado[0].temas.filter(p => p.nombre===modal.titulo)
        }
    }

  return (
    <div>
        {
            arreglo.length>0 ?
                <div className='container'>
                    <div className='row'>
                        <div>
                            <h5 className='title'>{arreglo[0].nombre}</h5>
                            <hr className='hr-gob'/>
                        </div>
                        {
                            arreglo[0].sub.length>0 ?
                                <div className='flex_info'>
                                    {
                                        arreglo[0].sub.map((item, i)=>(
                                            <div className="de_flex_info" key={i}>
                                                <div className="titulo_info">{item.titulo}</div>
                                                <div className='linea_info'/>
                                                {
                                                    item.temas.length>0 ?
                                                        <ul className="temas_info">
                                                            {
                                                                item.temas.map((tema, n) =>
                                                                    <li key={n} onClick={() => {
                                                                        if(arreglo[0].modal>0 && !tema.modal){
                                                                            setModal({
                                                                                ver: true,
                                                                                tipo: item.titulo,
                                                                                titulo: tema.nombre,
                                                                            })
                                                                        }
                                                                    }}>
                                                                        {
                                                                            arreglo[0].modal>0 && !tema.modal ?
                                                                                tema.nombre
                                                                            :
                                                                                <a href={`${arreglo[0].ruta}${item.carpeta ? item.carpeta : ""}${arreglo[0].indicador ? arreglo[0].indicador : ""}${tema.archivo}`} target="_new">{tema.nombre}</a>
                                                                        }
                                                                    </li>
                                                                )
                                                            }
                                                        </ul>
                                                    :
                                                        null
                                                }
                                            </div>
                                        ))
                                    }
                                </div>
                            :
                                null
                        }
                        <div id="mi_espacio"/>
                    </div>
                    <Modal
                        show={modal.ver}
                        centered
                        onHide={() => {
                            setModal({
                                ver: false,
                                tipo: "",
                                titulo: "",
                            })
                        }}
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>
                                <div className="modal_titulo">{modal.tipo}</div>
                                {modal.titulo}
                            </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                filtrado.length>0 ?
                                    filtradoTemas.length>0 ?
                                        filtrado[0].tipo==='Anual' ?
                                            <div className="flex_anual">
                                                Anual
                                                {/*
                                                    [...Array((arreglo[0].final - arreglo[0].inicial) + 1)].map((x, n) => {
                                                        return(
                                                            <a href={`${arreglo[0].ruta}${filtrado[0].carpeta ? filtrado[0].carpeta : ""}${arreglo[0].indicador ? arreglo[0].indicador : ""}${arreglo[0].inicial+n}_${filtradoTemas[0].archivo}`} target="_new" key={n}>
                                                                {arreglo[0].inicial+n}
                                                                <div>{filtradoTemas[0].archivo.split(".")[1]}</div>
                                                            </a>
                                                        )
                                                    })
                                                */}
                                                {
                                                    arreglo[0].anos.map((x, n) =>
                                                        <a href={`${arreglo[0].ruta}${filtrado[0].carpeta ? filtrado[0].carpeta : ""}${arreglo[0].indicador ? arreglo[0].indicador : ""}${x}_${filtradoTemas[0].archivo}`} target="_new" key={n}>
                                                            {x}
                                                            <div>{filtradoTemas[0].archivo.split(".")[1]}</div>
                                                        </a>
                                                    )
                                                }
                                            </div>
                                        :
                                            <div>
                                                <div className="flex_trimestral">
                                                    <trimestre>1er<h90> trimestre</h90></trimestre>
                                                    <trimestre>2do<h90> trimestre</h90></trimestre>
                                                    <trimestre>3er<h90> trimestre</h90></trimestre>
                                                    <trimestre>4to<h90> trimestre</h90></trimestre>
                                                </div>
                                                {
                                                    arreglo[0].anos.map((x, n) => {
                                                        let trimestres = 4
                                                        if(n===0 && arreglo[0].trimestre){
                                                            trimestres = arreglo[0].trimestre
                                                        }
                                                        return(
                                                            <div className="flex_trimestral">
                                                                {
                                                                    [...Array(trimestres)].map((xx, nn) => {
                                                                        return(
                                                                            <a href={`${arreglo[0].ruta}${filtrado[0].carpeta ? filtrado[0].carpeta : ""}${arreglo[0].indicador ? arreglo[0].indicador : ""}${x}_${nn + 1}Trim_${filtradoTemas[0].archivo}`} target="_new" key={n}>
                                                                                {x}
                                                                                <div className="quita_extension">{filtradoTemas[0].archivo.split(".")[1]}</div>
                                                                            </a>
                                                                        )
                                                                    })
                                                                }
                                                            </div>
                                                        )
                                                    })
                                                }
                                            </div>
                                    :
                                        null
                                :
                                    null
                            }
                        </Modal.Body>
                    </Modal>
                </div>
            :
                <Error404/>
        }
    </div>
  )
}

export default Informacion