import React, { useEffect } from 'react';
import Carousel from '../Carousel/Carousel';
import UltimasNoticias from '../UltimasNoticias/UltimasNoticias';
import './inicio.css';
//import { titular } from '../../json/titular';
import { Link } from 'react-router-dom';

const Inicio = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <>
      <Carousel/>
      <div className='container'>
        <div className='row'>
          <div className='secretaria_logo' >
            <div className='secretaria_logo_img' style={{ backgroundImage: `url("${window._my_icono}")` }}>

            </div>
          </div>
          <div className='secretaria_name'>
            <h1>{window._my_secretaria}</h1>
          </div>
          <div className='secretaria_funciones'>
            <h3 className='title'>FUNCIONES</h3>
            <hr className='hr-gob'/>
            <p><div dangerouslySetInnerHTML={{ __html: window._my_funciones }}/></p>
              <div className='btn_organigrama'>
                <Link to="Transparencia/Rubros/DetalleRubro/2.Estructura"><button type="button" className="btn btn-primary">Organigrama</button></Link>
                <Link to="Transparencia/Rubros/DetalleRubro/1.Normatividad"><button type="button" className="btn btn-primary">Normatividad</button></Link>
                {/*<Link to="Comites"><button type="button" className="btn btn-primary">Comits</button></Link>*/}
              </div>
              {
                window._my_redes.length>0 ?
                  <div className='secretaria-redes'>
                    {
                      window._my_redes.map((item, i) => 
                        <a href={item.link} target="_new"><i class={item.class} key={i}></i></a>
                      )
                    }
                  </div>
                :
                  null
              }
          </div>
        </div>
        {
          window._my_ultimasNoticias.length>0 ?
            <UltimasNoticias/>
          :
            null
        }
        {/*
        <div className='row'>
          <div className='notes_title'>
            <h3 className='title'>TITULAR</h3>
            <hr className='hr-gob'/>
          </div>
          <div className='section_notes'>
            {
              window._my_titular.map((item, index) =>(
                  <>
                    <div key={ index } className='sec_photo'>
                      <div className='img_secretario' style={{ backgroundImage: `url("${ item.imagen }")` }}>
                      </div>
                    </div>
                    <div className='sec_container'>
                      <div className='sec_name'>
                        <h3>{ item.nombre }</h3>
                      </div>
                      <div className='sec_function'>
                        <h4>Funcin del Secretario</h4>
                        <p>{ item.funcion }</p>
                        <div className='btn_agenda'>
                          <Link to="Agenda">Conoce la agenda del Secretario</Link>
                        </div>
                      </div>
                    </div>
                  </>
              ))
            }
          </div>
        </div>
        */}
        <div className='row'>
          <div className='section_map'>
            <div className='map_name'>
              <h3 className='title'>UBICACIN</h3>
              <hr className='hr-gob'/>
            </div>
            <iframe src={window._my_ubicacion}
                    width="100%" 
                    height="300" 
                    frameborder="0"
                    title="Ubicacin"
                    style={{border:0, marginBottom:50}} 
                    allowfullscreen>
            </iframe>
          </div>
        </div>   
      </div>
    </>
  )
}

export default Inicio